body {
  background: #eee;
  font-family: "Nunito", sans-serif;
}

#main h1 {
  color: #444;
  text-align: center;
}

#main {
  padding: 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 60%;
  height: 100%;
}

#book-list {
  top: 10%;
  position: absolute;
  width: 60%;
  padding: 0;
  overflow: auto;
  bottom: 45%;
}

#book-list li {
  display: inline-block;
  margin: 12px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #880e4f;
  -webkit-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);
          box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  color: #880e4f;
}

#add-book {
  background: #fff;
  padding: 20px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 400px;
}

#add-book .field {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

#add-book label {
  text-align: right;
  padding: 6px;
}

#add-book input,
#add-book select {
  margin: 4px 0;
  padding: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#add-book button {
  color: #fff;
  font-size: 2em;
  background: #ad1457;
  border: 0;
  padding: 0 10px;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  bottom: 10px;
  left: 10px;
}

#book-details {
  position: fixed;
  top: 0;
  right: 0;
  width: 40%;
  height: 100%;
  background: #ad1457;
  padding: 30px;
  overflow: auto;
  -webkit-box-shadow: -2px -3px 5px rgba(0, 0, 0, 0.3);
          box-shadow: -2px -3px 5px rgba(0, 0, 0, 0.3);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #fff;
}
#add-author {
  background: #fff;
  padding: 20px;
  position: fixed;
  left: 0;
  bottom: 160px;
  width: 400px;
}

#add-author .field {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

#add-author label {
  text-align: right;
  padding: 6px;
}

#add-author input,
#add-author select {
  margin: 4px 0;
  padding: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#add-author button {
  color: #fff;
  font-size: 2em;
  background: #ad1457;
  border: 0;
  padding: 0 10px;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  bottom: 10px;
  left: 10px;
}



